
import { songItemInter } from '@/detail/qq'
import { useRequest, useRequestInter, request } from '@/utils/request'
import { AxiosPromise } from 'axios'

const isPrd = process.env.NODE_ENV === 'production'
const baseUrl1:string = isPrd ? 'https://c.y.qq.com' : '/qq'
const baseUrl2:string = isPrd ? 'https://u.y.qq.com' : '/u.y.qq.com'

export const showList = (): useRequestInter => {
  return useRequest({
    baseURL: baseUrl1,
    url: '/splcloud/fcgi-bin/fcg_get_diss_by_tag.fcg',
    method: 'GET',
    params: {
      picmid: 1,
      rnd: Math.random(),
      g_tk: 732560869,
      loginUin: 0,
      hostUin: 0,
      format: 'json',
      inCharset: 'utf8',
      outCharset: 'utf-8',
      notice: 0,
      platform: 'yqq.json',
      needNewCode: 0,
      categoryId: 10000000, // 歌单类型Id
      sortId: 5,
      sin: 0,
      ein: 29
    }
  })
}
export const searchList = (): useRequestInter => {
  return useRequest({
    baseURL: baseUrl1,
    url: '/soso/fcgi-bin/client_search_cp',
    method: 'GET',
    params: {
      g_tk: 938407465,
      format: 'json',
      inCharset: 'utf-8',
      outCharset: 'utf-8',
      platform: 'h5',
      notice: 0,
      needNewCode: 1,
      zhidaqu: 1,
      catZhida: 1,
      t: 0,
      flag: 1,
      ie: 'utf-8',
      sem: 1,
      ggr: 0,
      perpage: 20,
      uin: 0,
      remoteplace: 'txt.mqq.all',
      _: 1459991037831
    }
  })
}
export const getSong = (song:songItemInter) : AxiosPromise<any> => {
  const songId = song.songmid
  const targetUrl = '/cgi-bin/musicu.fcg'
  const guid = '10000'
  const songmidList = [songId]
  const uin = '0'

  const fileType = '128'
  const fileConfig = {
    m4a: {
      s: 'C400',
      e: '.m4a',
      bitrate: 'M4A'
    },
    128: {
      s: 'M500',
      e: '.mp3',
      bitrate: '128kbps'
    },
    320: {
      s: 'M800',
      e: '.mp3',
      bitrate: '320kbps'
    },
    ape: {
      s: 'A000',
      e: '.ape',
      bitrate: 'APE'
    },
    flac: {
      s: 'F000',
      e: '.flac',
      bitrate: 'FLAC'
    }
  }
  const fileInfo = fileConfig[fileType]
  const file =
      songmidList.length === 1 &&
      `${fileInfo.s}${songId}${songId}${fileInfo.e}`

  const reqData = {
    req_0: {
      module: 'vkey.GetVkeyServer',
      method: 'CgiGetVkey',
      param: {
        filename: file ? [file] : [],
        guid,
        songmid: songmidList,
        songtype: [0],
        uin,
        loginflag: 1,
        platform: '20'
      }
    },
    loginUin: uin,
    comm: {
      uin,
      format: 'json',
      ct: 24,
      cv: 0
    }
  }
  const params = {
    format: 'json',
    data: JSON.stringify(reqData)
  }
  return request({
    url: targetUrl,
    baseURL: baseUrl2,
    method: 'GET',
    params
  })
}

export const getImg = (imgId: string, type: 'artist'|'album') : string => {
  let result = ''
  switch (type) {
    case 'artist':
      result = `T001R300x300M000${imgId}`
      break
    default:
      result = `T002R300x300M000${imgId}`
  }
  return `https://y.gtimg.cn/music/photo_new/${result}.jpg`
}
