export interface playDetailInter {
    id?: string;
    name?: string;
    imgurl?: string;
    creator?: {
      name?: string;
      isVip?: 0|1;
      avatarUrl?: string;
    }
}
export interface singerItem {
    id: number;
    mid: string;
    name: string;
}
export interface songItemInter {
    albumid?: string,
    albummid?: string,
    albumname?: string,
    singer?: singerItem[];
    songid: number;
    songmid: string;
    songname: string;
    isVip: boolean;
}

export const playListDetailMap = (list: JSONObject[]): playDetailInter[] => {
  const resultList: playDetailInter[] = list.map(detail => {
    const creator = detail.creator
    return {
      id: detail.dissid,
      name: detail.dissname,
      imgurl: detail.imgurl,
      creator: {
        name: creator.name,
        isVip: creator.isVip,
        avatarUrl: creator.avatarUrl
      }
    }
  })
  return resultList
}
export const songListDetailMap = (list: JSONObject[]):songItemInter[] => {
  const resultList: songItemInter[] = list.map(item => {
    const { albumid, albummid, albumname, singer, songid, songmid, songname, pay } = item
    const singerList = singer.map((s: singerItem) => {
      return {
        id: s.id,
        mid: s.mid,
        name: s.name
      }
    })
    return {
      albumid,
      albummid,
      albumname,
      singer: singerList,
      songid,
      songmid,
      songname,
      isVip: pay.payplay === 1
    }
  })
  return resultList
}
