import axios, { AxiosRequestConfig, AxiosResponse, CancelTokenSource } from 'axios'
import { ref, onUnmounted, Ref } from 'vue'

interface ajaxInter {
    ajax: (opt:AxiosRequestConfig, callback:(result:AxiosResponse)=>void)=>void
}

export interface requestOption {
  params?: JSONObject;
  data?: JSONObject;
}
export interface useRequestInter {
    request: (opt?: requestOption)=>Promise<any>;
    loading: Ref<boolean>;
    cancel: ()=>void;
}

let eeui: ajaxInter | null = null

const axiosRequest = axios.create({
  timeout: 12000
})
const getInstance = (opt: AxiosRequestConfig): Promise<any> => {
  const eeAjax = (opt: AxiosRequestConfig, eeTools: ajaxInter): Promise<any> => {
    return new Promise((resolve, reject) => {
      if (eeTools) {
        const options = {
          url: `${opt.baseURL}${opt.url}`,
          method: opt.method,
          timeout: 12000,
          headers: {
            referer: 'https://y.qq.com/',
            origin: 'https://y.qq.com'
          },
          data: {
            ...opt.params,
            ...opt.data
          }
        }
        eeTools.ajax(options, (res: any) => {
          if (res.status === 'success') {
            resolve({
              data: res.result,
              status: 200
            })
          } else {
            reject(res.result)
          }
        })
      }
    })
  }
  if (window.requireModuleJs) {
    eeui = window.requireModuleJs('eeui')
  }
  if (eeui) {
    return eeAjax(opt, eeui)
  } else {
    return axiosRequest(opt)
  }
}
export const useRequest = (options: AxiosRequestConfig): useRequestInter => {
  const loading = ref<boolean>(false)
  let source: CancelTokenSource | null = null
  const request = (opt: requestOption = {}): Promise<any> => {
    source = axios.CancelToken.source()
    const params = {
      ...options.params,
      ...opt.params
    }
    const data = {
      ...options.data,
      ...opt.data
    }
    const requestOption = {
      ...options,
      params,
      data,
      cancelToken: source.token
    }
    loading.value = true
    return new Promise((resolve, reject) => {
      getInstance(requestOption).then(res => {
        resolve(res)
        setTimeout(() => {
          loading.value = false
        }, 500)
      }).catch(err => {
        loading.value = false
        reject(err)
      })
    })
  }
  function cancel () {
    source && source.cancel()
  }
  // 组件卸载触发中断请求
  onUnmounted(() => {
    cancel()
  })
  return {
    request,
    loading,
    cancel
  }
}
export const request = getInstance
